<template><div id="app">
    <el-card body-style="magrin:0px" v-show="!show">
        <div slot="header" class="clearfix">
            <span>总览</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="getinfos">分析选中</el-button>

        </div>
<!--        -->
<!--        <el-input v-model.number="UserName">    <template slot="prepend">学号</template>-->
<!--            <el-button slot="append" icon="el-icon-user-solid" @click="login"></el-button></el-input>-->
<!--    </center>-->
        <center><el-table
            :data="datas"
        style="width: 100%"
            v-if="datas.length!==0"
              :row-class-name="tableRowClassName"
            :row-style="tableRowClassName"
            @selection-change="handleSelectionChange">
    >
            <el-table-column type="selection" width="50"/>
        <el-table-column
                label="Name"
                width="90">
            <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                    <el-tag size="medium">{{ scope.row.Name }}</el-tag>
                </div>

            </template>
        </el-table-column>
        <el-table-column
                label="NickName"
                width="160">
            <template slot-scope="scope">
                <i class="el-icon-s-order"></i>
                <span style="margin-left: 10px">{{ scope.row.Nick }}</span>
            </template>
        </el-table-column>
        <el-table-column
                label="Rating"
                width="130">
            <template slot-scope="scope">

                <span style="margin-left: 10px">{{ scope.row.Rating }}</span>
            </template>
        </el-table-column>
        <el-table-column
                label="ContestNum"
                width="130">
            <template slot-scope="scope">

                <span style="margin-left: 10px">{{ scope.row.Num }}</span>
            </template>
        </el-table-column>
        <el-table-column
                label="Top Rating"
                width="150">
          z  <template slot-scope="scope">

                <el-tag type="success">{{scope.row.Toprating}}</el-tag>

            </template>
        </el-table-column>
        <el-table-column
                label="Low Rating"
                width="150">
            <template slot-scope="scope">

                <el-tag type="warning">{{scope.row.Lowrating}}</el-tag>
            </template>
        </el-table-column>
        <el-table-column
                label="Average Rating"
                width="90">
            <template slot-scope="scope">
                <i class="el-icon-magic-stick"></i>
                {{scope.row.Avg}}
            </template>
        </el-table-column>
        <el-table-column
                label="Last Contest Time"
                width="180">
            <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <el-tag type="warning">{{getLocalTime(scope.row.Lastone.ratingUpdateTimeSeconds)}}</el-tag>
            </template>
        </el-table-column>

        <el-table-column type="expand">

            <template slot-scope="scope">
                <el-form label-position="left" inline style="width:50%;color: #99a9bf">
                    <el-form-item label="Last Contest Name">
                        <span>{{ scope.row.Lastone.contestName }}</span>
                    </el-form-item>
                    <el-form-item label="Last Contest Rank">
                        <span>{{ scope.row.Lastone.rank }}</span>
                    </el-form-item>
                    <el-form-item label="Last Five Rating">
                        <span>{{ scope.row.LastFive.join("-") }}</span>
                    </el-form-item>
                </el-form></template></el-table-column>
    </el-table>
        </center>
    </el-card>
    <el-card class="box-card" v-show="show" body-style="min-height:90%">
        <div slot="header" class="clearfix">
            <span>Details</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="show=!show">Back</el-button>
        </div>
        <v-chart :options="polar" autoresize/>
    </el-card>

</div>
</template>

<script>
    import axios from 'axios'
    import 'echarts/lib/chart/line'
    // axios.defaults.withCredentials = true;
    axios.defaults.headers.post['content-Type'] = 'appliction/x-www-form-urlencoded';
    export default {
        mounted: function () {
            this.getinfo();
        },

  name: 'app',
  data(){
    return{
      datas:[],
        selects:[],
        details:{
            "contests": [],
            "infos":[]
        },show:false,
        polar: {
            title: {
                text: '详细分析'
            },
            legend: {
                data: ['line']
            },

            tooltip : {
                trigger: 'item',
                formatter:(params)=> {
                    return "ContestID:" + this.details.contests[params.dataIndex].Name + "</br>" + "ContestID:" + this.details.contests[params.dataIndex].ID + "</br>" + "Time:" + this.getLocalTime(this.details.contests[params.dataIndex].Time)+"</br>"+params.seriesName+":"+params.data;

                },

                        axisPointer: {
                    type: 'shadow',
                    label: {
                        show:true,
                        backgroundColor: '#6a7985',


                        }
                    }

            },
            xAxis : [
                {
                    type : 'category',
                    boundaryGap : false,
                    data : []
                }
            ],
            yAxis : [
                {
                    type : 'value'
                }
            ],
            series: [

            ],
            animationDuration: 2000
        }

    }
  },
  methods:{
getinfo:function () {
    axios.get('/info').then(res=>{
        if (res.data){
            this.datas=res.data;
            // console.log(res.data)

        }
    })
},
      tableRowClassName:function ({rowIndex}) {
    if (rowIndex===0){return {"background-color": "#f0f9eb"}}
          // eslint-disable-next-line no-console
    //console.log(row,rowIndex)
    // if (row.Status!=="OK"){
    //     return {"background-color": "#fdf5e6"}}
    //       return '';
      },
      getLocalTime:function (nS) {
        return new Date(parseInt(nS) * 1000).toLocaleString().replace(/:\d{1,2}$/,' ');
    },handleSelectionChange:function (val) {
          // eslint-disable-next-line no-console
          console.log(val)
          var lists=[]
          val.forEach((item)=> {

lists.push(item['Nick'])
              // eslint-disable-next-line no-console
              console.log(item['Nick'])
          })
          this.selects=lists

      },
      getinfos:function () {
    // var datas=new FormData();
          // var i = 0
          //
          // // eslint-disable-next-line no-console
          // console.log(datas, this.selects[i].Nick)

        axios.post("/detail",{"ID":this.selects}).then((res)=> {
                  // eslint-disable-next-line no-console
            console.log(res.data)
            this.details=res.data
            let contests=res.data.contests
            contests.forEach((contest)=>{
              this.polar.xAxis[0].data.push(contest.ID)

            })
            let infos=this.details.infos

                for(let is in Object.keys(infos)){
                    this.polar.legend.data.push(Object.keys(infos)[is])

                    this.polar.series.push({
                        name:Object.keys(infos)[is],
                        type:'line',

                        areaStyle: {normal: {},origin:"start"},
                        connectNulls:true,


                        data:infos[Object.keys(infos)[is]]
                    })
                }

       })
this.show=true
}

        }
      }



</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

}
   .el-progress-bar{
        width: 70%;
    }
.echarts {
    width: 100%;
    height: 900px;
}

</style>
